.App {
  text-align: center;
}

body {
  background: #FE9164;
  color: #FFFFE6;
  font-family: "Roboto", sans-serif;
}

input {
  background: border-box;
  border-color: #FFFFE6;
  color: #FFFFE6;
  border-top: none;
  outline: none;
  width: 20.5rem;
  padding: 0.313rem;
}

button {
  background-color: #000000;
  color: #FFFFE6;
  border: none;
  padding: .8rem 2rem;
  margin-top: 2rem;
  text-transform: uppercase;
  letter-spacing: 2px;
}